body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-primary: #746CE7; 
  --font-primary: 'Source Sans Pro';
}

body {
  background: #F8F9FD !important;
  font-family: 'Source Sans Pro', sans-serif;
  font-family: var(--font-primary), sans-serif;
}
/* 
============================
Navbar
============================
*/

.navbar__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  background: #746CE7;
  padding: 1.2em;
}

.navbar__menu--brand {
  font-size: 1.6em;
  font-weight: 700;
  font-family: Helvetica, sans-serif;
  color: white !important;
  margin: 1em;
}

.navbar__menu--brand span {
  color: #E2D5EA;
}

.navbar__menu--item {
  font-family: var(--font-primary);
  color: #F8F9FD !important;
  cursor: pointer;
  margin: 0 1em;
  -webkit-transition: .3s ease all;
  transition: .3s ease all;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navbar__menu--item:hover {
  color: #e5ddf7 !important;
}

.navbar__profile {
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navbar__profile p {
  color: #F8F9FD;
  /* margin: .45em; */
  margin-bottom: 0;
}

.navbar__profile img {
  max-width: 50px;
  border-radius: 65px;
  margin: 0 1em;
}

@media screen and (max-width: 768px) {
  .navbar__menu {
    justify-content: center;
  }

  .navbar__menu--brand {
    text-align: center;
  }

  .navbar__menu--item {
    display: none;
  }
  
  .navbar__profile {
    display: none;
  }
}
/* 
============================
Table
============================
*/

.ctable {
  margin: 1em;
  padding: 1em;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.04);
  background: white;
}

.ctable__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 1.85em .75em;
}

.btn_group {
  flex-wrap: wrap;
}

.btn_group > * {
  margin: 0 .45em;
}

@media screen and (max-width: 650px) {
  .ctable__head {
    flex-direction: column;
  }
  .btn_group > * {
    margin: .45em;
  }
}

.ctable--title {
  font-weight: 400;
  font-size: 1.2em;
  /* margin: 1em 0; */
}

.ctable--title span {
  color: #8184ac;
}

.ctable__body--title span.amount {
  color: #343863;
  font-weight: 700;
}

/* .ctable__box {
  padding: 1em;
  margin: 1em;
} */

.ctable__header {
  background: #F5F6FA;
  margin: .75em;
  padding: 1em;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: .75em;
}

.ctable__header h6 {
  text-align: left;
  text-transform: uppercase;
  font-size: .875em;
  font-weight: bold;
  color:#A3A6B4;
  margin-bottom: 0;
}

.ctable__body {
  margin: .75em 0;
}

.ctable__body--cell {
  margin: .75em;
  padding: 1em;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: .75em;
  border-bottom: 1px solid rgba(201, 204, 223, 0.705);
}

.ctable__body--col article {
  margin: 1em 0;
  padding-bottom: .75em;
  border-bottom: 1px solid #f5f5f5;
}

.ctable__body--cell .ctable__body--title {
  font-size: .875em;
  color: #4D4F5C;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .ctable {
    margin: .45em;
  }

  .ctable__header {
    margin: 0;
    padding: 1em;
    grid-gap: 1em;
  }

  .ctable__body--cell {
    margin: 0;
    padding: 1em;
    grid-gap: 1em;
  }
}

/* Custom Badge */
.cbadge {
  background: rgba(255, 202, 131, 0.50);
  color: rgba(0, 0, 0, 0.50);
  border-radius: 65px;
  padding: .55em 1em;
  font-size: 0.75em;

  display: none;
  opacity: 0;
  visibility: hidden;
}

.is-active {
  display: inline;
  display: initial;
  opacity: 1;
  visibility: visible;
}
/* 
============================
Results
============================
*/

.grid__results {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  align-items: center;
  justify-items: center;
  grid-gap: 1em;
  margin: 2em 0;
}

.card__result {
  background: white;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.07);
  min-width: 300px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.card__result img {
  max-width: 100px; 
  padding: .65em;
  margin: 1em;
}

.card__result div {
  padding: .65em;
  margin: 1em;
}

.card__result h4 {
  margin: 1em 0 .45em;
  color: #4D4F5C;
}
/* 
============================
Graphs
============================
*/

/* div.container {
  margin: 2em 0;
} */

.graph__box {
  background: white;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.05);
  padding: 2em;
}

@media screen and (max-width: 650px) {
  .graph__box {
    padding: 1em;
  }
}
/* 
============================
Footer
============================
*/

footer {
  background: white;
  color: #A4A7B5;
  font-size: .875em;
  text-align: center;
  padding: 5em 1em;
}
